import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, message } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import SearchBar from './SearchBar';
import { TableData } from '../../../components/common';
import { Btn } from '../../../components/common/button/index';
import { getAdmissionReceipts } from '../../../services/admissionReceipts';
import { moneyToString } from '../../../utils/formatUtil';

class AdmissionReceipts extends Component {
  state = {
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'K hiu',
      dataIndex: 'kyhieu',
      key: 'kyhieu',
    },
    {
      title: 'S phiu',
      dataIndex: 'sophieu',
      key: 'sophieu',
    },
    {
      title: 'Ngy thu',
      dataIndex: 'ngaythu',
      key: 'ngaythu',
    },
    {
      title: 'M SV',
      dataIndex: 'masinhvien',
      key: 'masinhvien',
    },
    {
      title: 'H v tn',
      dataIndex: 'hovaten',
      key: 'hovaten',
    },
    {
      title: 'Tn lp',
      dataIndex: 'tenlop',
      key: 'tenlop',
    },
    {
      title: 'S tin',
      dataIndex: 'sotien',
      render: text => moneyToString(text),
      key: 'sotien',
    },
    {
      title: 'Ni dung',
      dataIndex: 'noidung',
      key: 'noidung',
    },
    {
      title: 'Ngi thu',
      dataIndex: 'nguoithu',
      key: 'nguoithu',
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: () => (
        <Fragment>
          <Btn onClick={this.xemphieu}>Xem phiu</Btn>
          <Btn onClick={this.huyphieu}>Hy phiu</Btn>
        </Fragment>
      ),
    },
  ];

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  xemphieu = () => {
    message.success('Bn va nhn xem phiu');
  }

  huyphieu = () => {
    message.warning('Bn va nhn hy phiu');
  }

  render() {
    return (
      <Card>
        <Row>
          <Col span={24}>
            <SearchBar />
          </Col>
          <Col span={24}>
            <TableData
              columns={this.columns}
              paramSearch={this.state.paramSearch}
              service={getAdmissionReceipts}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AdmissionReceipts />
  </Page>
);



